import React, { useState, useMemo, useEffect, useCallback } from 'react'
import styled from 'styled-components'
import {
    useUserTransactions,
    useUserPositions,
    useMiningPositions,
} from '../contexts/User'
import TxnList from '../components/TxnList'
import Panel from '../components/Panel'
import { formattedNum } from '../utils'
import Row, { AutoRow, RowFixed, RowBetween } from '../components/Row'
import { AutoColumn } from '../components/Column'
import UserChart from '../components/UserChart'
import PairReturnsChart from '../components/PairReturnsChart'
import PositionList from '../components/PositionList'
import MiningPositionList from '../components/MiningPositionList'
import { TYPE } from '../Theme'
import { ButtonDropdown, ButtonLight } from '../components/ButtonStyled'
import { PageWrapper, ContentWrapper, StyledIcon } from '../components'
import DoubleTokenLogo from '../components/DoubleLogo'
import { Bookmark, Activity } from 'react-feather'
import Link from '../components/Link'
import { FEE_WARNING_TOKENS } from '../constants'
import { BasicLink } from '../components/Link'
import { useMedia } from 'react-use'
import Search from '../components/Search'
import { useSavedAccounts } from '../contexts/LocalStorage'

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types, react/prop-types
function AccountPage({ account }) {
    // get data for this account
    const transactions = useUserTransactions(account)
    const positions = useUserPositions(account)

    // get data for user stats
    const transactionCount =
        transactions?.swaps?.length +
        transactions?.burns?.length +
        transactions?.mints?.length

    // get derived totals
    let totalSwappedUSD = useMemo(() => {
        return transactions?.swaps
            ? transactions?.swaps.reduce((total, swap) => {
                  return total + parseFloat(swap.amountUSD)
              }, 0)
            : 0
    }, [transactions])

    // if any position has token from fee warning list, show warning
    const [showWarning, setShowWarning] = useState(false)
    useEffect(() => {
        if (positions) {
            for (let i = 0; i < positions.length; i++) {
                if (
                    FEE_WARNING_TOKENS.includes(positions[i].pair.token0.id) ||
                    FEE_WARNING_TOKENS.includes(positions[i].pair.token1.id)
                ) {
                    setShowWarning(true)
                }
            }
        }
    }, [positions])

    // settings for list view and dropdowns

    useEffect(() => {
        window.scrollTo({
            behavior: 'smooth',
            top: 0,
        })
    }, [])

    return (
        <PageWrapper>
            <ContentWrapper>
                <iframe
                    title='Swap Widget'
                    src='https://shriya-widgetsapp-wmn5n7rc5q-uc.a.run.app'
                    frameBorder='{0}'
                    height='900'
                    width='100%'
                    style='display = block;'
                    sandbox='allow-scripts'
                    allow='accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture'
                    allowFullScreen=''
                ></iframe>
            </ContentWrapper>
        </PageWrapper>
    )
}

export default AccountPage
